@import url(https://fonts.googleapis.com/css?family=Open + Sans:400|Raleway:300);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-image: linear-gradient(120deg, #f6d365 0%, #fda085 100%);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

/* properties of individual Node */
.node {
  width: 25px;
  height: 25px;
  border: 1px solid #fdff85;
  display: inline-block;
  border-radius: 100%;
}

.node:hover {
  transition: 0.3s;
  -webkit-transform: scale(1);
          transform: scale(1);
  border: 1px solid orangered;
  background-color: rgba(255, 231, 179, 0.986);
}

/* Start node properties */
.node-start {
  background-color: rgb(6, 255, 6);
}

/* Finish Node properties */
.node-finish {
  background-color: rgb(255, 58, 58);
}

/* Animations to visited nodes */
.node-visited {
  -webkit-animation-name: visitedAnimation;
          animation-name: visitedAnimation;
  /* Call the keyframes defined below */
  -webkit-animation-duration: 1.5s;
          animation-duration: 1.5s;
  -webkit-animation-timing-function: ease-in-out;
          animation-timing-function: ease-in-out;
  -webkit-animation-delay: 0;
          animation-delay: 0;
  -webkit-animation-direction: alternate;
          animation-direction: alternate;
  -webkit-animation-iteration-count: 1;
          animation-iteration-count: 1;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  -webkit-animation-play-state: running;
          animation-play-state: running;
}

@-webkit-keyframes visitedAnimation {
  0% {
    -webkit-transform: scale(0.3);
            transform: scale(0.3);
    background-color: rgba(180, 0, 0, 0.75);
  }

  50% {
    background-color: rgba(255, 16, 16, 0.966);
  }

  75% {
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
    background-color: rgb(255, 94, 0);
  }

  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
    background-color: rgba(255, 187, 0, 0.986);
  }
}

@keyframes visitedAnimation {
  0% {
    -webkit-transform: scale(0.3);
            transform: scale(0.3);
    background-color: rgba(180, 0, 0, 0.75);
  }

  50% {
    background-color: rgba(255, 16, 16, 0.966);
  }

  75% {
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
    background-color: rgb(255, 94, 0);
  }

  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
    background-color: rgba(255, 187, 0, 0.986);
  }
}

/* Node wall Style */
.node-wall {
  background-color: rgb(16, 0, 75);
}

/* Handle animations for displaying the shortest path */
.node-shortest-path {
  -webkit-animation-name: shortestPath;
          animation-name: shortestPath;
  /* Keyframes defined below */
  -webkit-animation-duration: 1.5s;
          animation-duration: 1.5s;
  -webkit-animation-timing-function: ease-out;
          animation-timing-function: ease-out;
  -webkit-animation-delay: 0;
          animation-delay: 0;
  -webkit-animation-direction: alternate;
          animation-direction: alternate;
  -webkit-animation-iteration-count: 1;
          animation-iteration-count: 1;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  -webkit-animation-play-state: running;
          animation-play-state: running;
}

@-webkit-keyframes shortestPath {
  0% {
    -webkit-transform: scale(0.6);
            transform: scale(0.6);
    background-color: rgb(90, 0, 50);
  }

  50% {
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
    background-color: rgb(4, 0, 250);
  }

  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
    background-color: rgb(22, 228, 255);
  }
}

@keyframes shortestPath {
  0% {
    -webkit-transform: scale(0.6);
            transform: scale(0.6);
    background-color: rgb(90, 0, 50);
  }

  50% {
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
    background-color: rgb(4, 0, 250);
  }

  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
    background-color: rgb(22, 228, 255);
  }
}

.grid {
  margin: 100px 0 0;
}

.visualize {
  margin-top: 5px;
  width: 220px;
  height: 50px;
  border: none;
  outline: none;
  color: #fff;
  background: orangered;
  cursor: pointer;
  position: relative;
  z-index: 0;
  border-radius: 10px;
  text-transform: uppercase;
  letter-spacing: 10px;
  font-weight: bold;
}

.visualize:before {
  content: "";
  background: linear-gradient(
    45deg,
    #ff0000,
    #ff7300,
    #fffb00,
    #48ff00,
    #00ffd5,
    #002bff,
    #7a00ff,
    #ff00c8,
    #ff0000
  );
  position: absolute;
  top: -2px;
  left: -2px;
  background-size: 400%;
  z-index: -1;
  -webkit-filter: blur(5px);
          filter: blur(5px);
  width: calc(100% + 4px);
  height: calc(100% + 4px);
  -webkit-animation: glowing 20s linear infinite;
          animation: glowing 20s linear infinite;
  opacity: 0;
  transition: opacity 0.3s ease-in-out;
  border-radius: 10px;
}

.visualize:active {
  color: orangered;
}

.visualize:active:after {
  background: transparent;
}
.visualize:hover:before {
  opacity: 1;
}
.visualize:after {
  z-index: -1;
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  background: orangered;
  left: 0;
  top: 0;
  border-radius: 10px;
}
@-webkit-keyframes glowing {
  0% {
    background-position: 0 0;
  }
  50% {
    background-position: 400% 0;
  }
  100% {
    background-position: 0 0;
  }
}
@keyframes glowing {
  0% {
    background-position: 0 0;
  }
  50% {
    background-position: 400% 0;
  }
  100% {
    background-position: 0 0;
  }
}

